body {
    margin: 0;
    background: linear-gradient(to right, rgb(93, 73, 110), rgb(29, 44, 51));
    font-family: 'Courier New', Courier, monospace;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

button {
    cursor: pointer;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}
.App {
    text-align: center;
}

.center {
    opacity: 1;
    display: flex;
    justify-content: center;
}

.particles {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: -1;
}
    .Tilt {
        opacity: 1;
        height: 200px;
        width: 300px;
        padding: 1px;
        padding-top: 0px;
        padding-bottom: 0px;
    }
.form {
    width: 600px;
    justify-content: center;
    background-color: linear-gradient(to right, rgb(11, 120, 142), rgb(84, 57, 96));
    background-image: linear-gradient(white 2px, transparent 2px), linear-gradient(90deg, white 2px, transparent 2px), linear-gradient(rgba(255, 255, 255, .3) 1px, transparent 1px), linear-gradient(90deg, rgba(255, 255, 255, .3) 1px, transparent 1px);
    background-size: 100px 100px, 100px 100px, 20px 20px, 20px 20px;
    background-position: -2px -2px, -2px -2px, -1px -1px, -1px -1px;
}

.bt2 {
    background-color: rgb(67, 66, 149);
}
